.content {
  height: 350px;
  margin-top: 50px;
  margin-left: 50px;
  margin-right: 50px;
  align-items: center;
  justify-content: center;
}

.headshot {
  height: 450px;
  width: 450px;
  padding-left: 30px;
  padding-top: 50px;
  padding-right: 30px;
  margin-right: 35px;
  float: left;
}

h2 {
  padding-top: 50px;
  font-size: 75px;
  font-family: Georgia, "Times New Roman", Times, serif;
  color: purple;
  margin-left: 125px;
}

p {
  font-family: Georgia, "Times New Roman", Times, serif;
  font-size: 30px;
  margin-left: 200px;
}
.icons {
  display: flex;
  justify-content: center;
}

.glyph {
  margin-right: 20px;
}

main {
  margin-top: 100px;
}

h3 {
  text-align: center;
  font-size: 50px;
}

.row {
  margin-left: 45px;
  margin-right: 45px;
  margin-bottom: 30px;
}

.cardContainerM {
  border: black solid 5px;
}

.card-body {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgb(0, 0, 0, 0.7);

  overflow: hidden;
  width: 100%;
  height: 0;
  padding-bottom: 30px;
  transition: 0.5s ease;
}

.card-main {
  height: 650px;
  width: 400px;
}
.card {
  border-style: solid;
  border-color: black;
  border-radius: 10px;
  border-bottom: 10px solid #478ba7;
}
.card-title {
  font-size: 20px;
}

.card-text {
  font-size: 15px;
  margin-left: auto;
}

.card-title,
.card-text {
  color: white;
}

@media only screen and (min-width: 900px) {
  .cardContainer:hover .card-body {
    height: 100%;
  }
}
